import axios from 'axios'
import { gitApiPrefix } from '@/api/config.js'

// 查询所有的工程
export const findProjects = (params) => axios.get(`${gitApiPrefix}/groups/13/projects/?private_token=RfRyfGXCnwJF62boNdKg&order_by=updated_at`, { params })
// 查询工程下面的所有分支
export const findBranchsByProject = (params) => axios.get(`${gitApiPrefix}/projects/${params.projectId}/repository/branches/?private_token=RfRyfGXCnwJF62boNdKg&per_page=50`, { params })
// 创建merge request
export const createMergeRequest = (params) => axios.post(`${gitApiPrefix}/projects/${params.projectId}/merge_requests/?private_token=RfRyfGXCnwJF62boNdKg`, params)
// 合并操作
export const acceptMergeRequest = (params) => axios.put(`${gitApiPrefix}/projects/${params.projectId}/merge_requests/${params.mrId}/merge/?private_token=RfRyfGXCnwJF62boNdKg`, params)
// 关闭merge request
export const closeMergeRequest = (params) => axios.put(`${gitApiPrefix}/projects/${params.projectId}/merge_requests/${params.mrId}/?private_token=RfRyfGXCnwJF62boNdKg&state_event=close`, params)
// 创建tag
export const createTagByProject = (params) => axios.post(`${gitApiPrefix}/projects/${params.projectId}/repository/tags/?private_token=RfRyfGXCnwJF62boNdKg`, params)
// 查询tag
export const findTagsByProject = (params) => axios.get(`${gitApiPrefix}/projects/${params.projectId}/repository/tags/?private_token=RfRyfGXCnwJF62boNdKg`)
// 删除tag
export const removeTagByProject = (params) => axios.delete(`${gitApiPrefix}/projects/${params.projectId}/repository/tags/${params.tagName}?private_token=sA1cAMxP7rYqosRz7DcG`, {})
// 查询merge request
export const findMergeRequest = (params) => axios.get(`${gitApiPrefix}/projects/${params.projectId}/merge_requests?search=${params.search}&state=opened&private_token=RfRyfGXCnwJF62boNdKg`)
